@charset "UTF-8";
body {
  margin: 0;
  padding: 0;
  background-color: skyblue;
  font-family: Myriad Pro、Myriad、Helvetica Neue、Helvetica、Liberation Sans、Arial、sans-serif;
}

body h1 {
  color: #fff;
  font-size: 30px;
}

body ul {
  list-style: none;
}

body ul li {
  line-height: 36px;
}

body ul li a {
  color: #fff;
  text-decoration: none;
}

body ul li:even {
  background-color: orange;
}
